import { NavLink } from 'react-router-dom';
function Navigation() {
  return (
    <nav>
      <ul>
        <li>
          <NavLink
            to="/"
            exact
            className="navLink"
            activeClassName="activeNavLink"
          >
            Home
          </NavLink>
        </li>
        <li>
          <NavLink
            to="/movies"
            className="navLink"
            activeClassName="activeNavLink"
          >
            Movies
          </NavLink>
        </li>
      </ul>
    </nav>
  );
}
export default Navigation;
